.wrapper{
    padding: 50px;
    position: relative;
    font-size: 35px;
}
.dark{
    padding: 200px;
    background-color: black;
    color: white;
    text-align: center;
}
.Light{
    padding: 200px;
    text-align: center;
    background-color: lightyellow;
    color: black;
}
.navbar{
    position: relative;
    z-index: 2;
    display: flex;
    gap: 50px;
    font-size: 45px;
    padding-bottom: 20px;
    transition: 0.3s;
    & a {
        text-decoration: none;
        color: red;
        transition: 0.3s;
        &:hover{
            color: black;
        }
    }
    & a.active{
        transition: 0.3s;
        color: green;
    }
}