body{
  background-color:#BE3455;
}
.wrapper {
  padding: 50px;
  position: relative;
  font-size: 35px;
  display: flex;
}

.dark {
  padding: 200px;
  background-color: black;
  color: white;
  text-align: center;
}

.Light {
  padding: 200px;
  text-align: center;
  background-color: lightyellow;
  color: black;
}

.navbar {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: 50px;
  font-size: 45px;
  padding-bottom: 20px;
  transition: 0.3s;
}
.navbar a {
  text-decoration: none;
  color: yellowgreen;
  transition: 0.3s;
}
.navbar a:hover {
  color: black;
}
.navbar a.active {
  transition: 0.3s;
  color: black;
}/*# sourceMappingURL=style.css.map */
.file-input-label {
  display: inline-block;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

/* Стилизация input для скрытия его текстовой метки */
.file-input-label input[type="file"] {
  position: absolute;
  font-size: 100px;
  opacity: 0;
  right: 0;
  top: 0;
}

/* Стилизация input при наведении курсора */
.file-input-label input[type="file"]:hover {
  cursor: pointer;
}